$btn-border: 1px solid rgb(107, 107, 107);

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Poppins';
}

body {
    background-image: url("../images/paper-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}

.head-title {
    font-size: clamp(2.2rem, 3vw, 3rem);
    margin-bottom: 30px;
    text-align: center;
    text-transform: uppercase;
}

main {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.etch-a-sketch {
    text-align: center;
}

.controls {
    margin-bottom: 20px;
}

input[type="text"]{
    width: 295px;
    padding: 10px;
    margin-bottom: 10px;
}

button {
    font-weight: 600;
    padding: 10px;
    cursor: pointer;
}

.black-btn:hover {
    border: $btn-border;
    color: white;
    background-color: black;
}

.rainbow-btn:hover {
    border: 1px solid rgb(107, 107, 107);
    background-color: #91d370;
    background-image: linear-gradient(319deg, #91d370 0%, #bca0ff 37%, #f2cd54 100%);
}

.eraser-btn:hover{
    background-color: white;
    border: $btn-border
}

.main-grid {
    display: grid;
    width: 600px;
    height: 600px;
    border: 5px solid black;
}

.grid-element {
    background-color: white;
    border: 1px solid rgb(180, 180, 180);
    cursor: pointer;
}

@media screen and (max-width: 450px) {

    .head-title{
        margin-bottom: 30px;
    }

    .main-grid{
        width: 300px;
        height: 300px;
        margin: 0 auto;
    }

    input[type="text"]{
        width: 85%;
    }
}